import React from 'react';

import utils from '../../utils';

import './style.css';

const items = utils.newArray(4).map(i => {
  return {
    title: utils.localize(`view8.title${i + 1}`),
    content: utils.localize(`view8.content${i + 1}`)
  };
});

class View8 extends React.Component {
  constructor(props) {
    super(props);
  }

  renderItem({src, icon, title, content}) {
    return (
      <div className="view-item-container">
        <div className="view-item-title">
          <h2>{title}</h2>
        </div>
        <p>{content}</p>
      </div>
    );
  }

  render() {
    return (
      <div className="view-container view8">
        <img src={require('../../sources/aboutus/view8.jpg').default} />
        <div className="view-content-background"></div>
        <h2 className="view8-title">{utils.localize('view8.title')}</h2>
        <div className="view-content-container">
          {
            items.map(item => this.renderItem(item))
          }
        </div>
      </div>
    );
  }
}

export default View8;