import React from 'react';

import utils from '../../utils';

import './style.css';

const items = utils.newArray(4).map(i => {
  return {
    src: require(`../../sources/aboutus/view6_${i + 1}.jpg`).default,
    icon: require(`../../sources/aboutus/view6_${i + 1}.png`).default,
    title: utils.localize(`view2.img.title${i + 1}`),
    content: utils.localize(`view6.content${i + 1}`)
  };
});

class View6 extends React.Component {
  constructor(props) {
    super(props);
  }

  renderItem({src, icon, title, content}) {
    return (
      <div className="view-item-container">
        <div className="view-content-text-container">
          <p>{content}</p>
        </div>
        <div className="view-content-img-container">
          <img src={src} />
          <div className="view-content-title-container">
            <img className="view-content-icon-container" src={icon} />
            <h2>{title}</h2>
          </div>
        </div>
      </div>
    );
  }

  render() {
    return (
      <div className="view-container view6">
        <h2 className="view6-title">{utils.localize('view6.title')}</h2>
        <div className="view-content-container">
          {
            items.map(item => this.renderItem(item))
          }
        </div>
      </div>
    );
  }
}

export default View6;