import React from 'react';

import utils from '../../utils';

import './style.css';

const items = utils.newArray(6).map(i => {
  return {
    icon: require(`../../sources/aboutus/view7_${i + 1}.png`).default,
    title: utils.localize(`view7.item${i + 1}.title`),
    content: utils.localize(`view7.item${i + 1}.content`)
  }
});

class View7 extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="view-container view7">
        <div className="view-content-container">
          <div className="view-content-img-container">
            <img src={require('../../sources/aboutus/view7.jpg').default} />
          </div>
          <div className="view-content-text-container">
            <div className="view-content-title-container">
              <h2>{utils.localize('view7.title')}</h2>
              <p>{utils.localize('view7.content')}</p>
            </div>
            <div className="view-content-items-container">
              {
                items.map((item, i) => {
                  return (<div className="view-content-item" key={i}>
                    <img src={item.icon} />
                    <h2>{item.title}</h2>
                    <p>{item.content}</p>
                  </div>);
                })
              }
            </div>    
          </div>
        </div>
      </div>
    );
  }
}

export default View7;